<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>{{isEdit()?'Edit':'Add'}} {{accountType==1?'Email':'Mobile'}}</h1>
       <div class="chartWrap">
        </div>
    </section>
    <div class="centerWrap">
        <div class="center">
              <div class="enterEamil" v-if="isEdit()">
                  Enter the {{accountType==1?'Email address':'Mobile '}} you want to add. We will send you a code to add this {{accountType==1?'Email address':'Mobile '}} to your acount
              </div>
               <p style="color: #666666;" v-if="isEdit()">Old Email Address</p>
               <span class="title" v-if="isEdit()">{{accountType==1?'':'AE +'}}{{accountType==1?user.email:user.mobile}}</span>
           <div class="emailWrap">
               <img src="../common/images/orders.png" v-if="accountType==1"> 
               <span v-if="accountType==2">AE +97 <a class="iconfont back"> &#xe671; </a></span>
               <van-field  v-model="username" v-if="accountType==1"
                    placeholder="Enter New Email Address"  v-validate="'required|email'" name="email"             
                />
               <van-field  v-model="username" v-else
                    placeholder="Enter New Mobile"  v-validate="'required|mobile'" name="mobile"             
                /> 
                
                </div>
                <font v-if="accountType==1" style="color: red;" v-show="errors.has('email')">{{ errors.first('email') }}</font>
                <font v-else style="color: red;" v-show="errors.has('mobile')">{{ errors.first('mobile') }}</font>
             <div class="sada" v-if="!isEdit()">
                We will send a code to the New {{accountType==1?'Email':'Mobile'}} above, please enter the code next.
            </div>
               
           
            <van-button type="danger" @click="applyEdit">Send</van-button>
        </div>

       
    </div>
  </div>
</template>

<script>

import { sendEmail, sendSms } from "@/api/msg";
import {mapGetters} from 'vuex';
import validate from "@/utils/validate";
export default {
  name: 'addMobile',
  data () {
    return {
     username:"",
      accountType:this.$route.query.accountType
    }
  },
  computed: {
      ...mapGetters(['user'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    isEdit(){
      if(this.accountType==1&&this.user.email || this.accountType==2 &&this.user.mobile){
        return true;
      }
      return false;
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    applyEdit() {
      this.$validator.validateAll().then(result => {
        if (result) {
            let func = this.accountType==1 ? sendEmail : sendSms;
            let code=this.accountType==1?'updateEmail':'reMobile';
            func(this.username, code).then(res => {
              this.$toast("Code send successfully");
              this.$router.push({
                path: "verifyCode",
                query: {
                  username: this.username,
                  type: code,
                  accountType: this.accountType
                }
              });
            });
         }  
      });

    }


  }
}
</script>

<style lang="scss" scoped>
  .centerWrap{
      width: 100%;
      padding-top: 10px;
      background:rgba(246,246,246,1);
  }
  .center{
      width: 100%;
      padding: 0 6%;
      background-color: #fff;
      font-size: 13px;
       p{
        padding-top: 12px;
      }
  }
  .title{
      width: 100%;
      text-align: center;
      display: block;
    font-size: 16px;
    padding: 10px 0 20px 0;
  }
  .center .van-cell{
        border-bottom: 1px solid #f4f4f4;
        width: 95%;
        padding: 10px 0;
        height: 50px;
  }
  
  .center .van-button--danger{
        width: 100%;   
    }
.emailWrap{
    width: 95%;
    display: flex; align-items: center; justify-content: center;
    // padding-bottom: 99px;
    img{
      height: 19px;
      width: 19px;
      margin: 0 10px 5px 0;
    }
}
.emailWrap span{
    font-size: 15px;
    width: 135px;
    display: flex; align-items: center; justify-content: space-evenly;
}
.emailWrap span a{
    font-size: 20px;
    
}
.sada{
      color: #7d7e80;
      padding: 15px 0 20px 0;
  }
.enterEamil{
    font-size: 13px;
    color: #666666;
    padding-top: 19px;
    padding-bottom: 15px;
}

</style>



